<template>
  <div class="wrapper">
    <img class="wrapper__img" src="@/assets/编组.svg" alt="">
    <input class="wrapper__input" type="text" placeholder="请输入用户名" v-model="username">
    <input class="wrapper__input" type="password" placeholder="请输入密码" v-model="password">
    <div class="wrapper__login" @click="handleLogin">登录</div>
    <div class="wrapper__link">
      <router-link to="/register">
        <span>立即注册</span>
      </router-link>
      <span class="wrapper__link__gap">|</span>
      <span>忘记密码</span>
    </div>
    <Toast v-if="toastInfo.show" :message="toastInfo.message"/>
  </div>
</template>

<script>
import { reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'
import { post } from '@/utils/request'
import Toast, { useToastEffect } from '@/components/Toast.vue'

export default {
  name: 'Login',
  components: { Toast },
  setup () {
    const router = useRouter()
    const loginInfo = reactive({
      username: '',
      password: ''
    })
    const {
      toastInfo,
      showToast
    } = useToastEffect()
    const handleLogin = async () => {
      if (!loginInfo.username) return showToast('用户名不能为空')
      if (!loginInfo.password) return showToast('密码不能为空')
      try {
        const result = await post('/api/user/login', {
          username: loginInfo.username,
          password: loginInfo.password
        })
        if (result?.errno !== 0) return showToast(result.desc)
        localStorage.setItem('loginTime', new Date().getTime())
        router.push('/')
      } catch (e) {
        showToast(e.message)
      }
    }
    return {
      ...toRefs(loginInfo),
      toastInfo,
      handleLogin
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";

.wrapper {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  padding: 0 .4rem;

  &__img {
    display: block;
    width: .66rem;
    height: .66rem;
    margin: 0 auto .4rem;
  }

  &__input {
    width: 100%;
    height: .48rem;
    margin-bottom: .16rem;
    background: #F9F9F9;
    border: .01rem solid rgba(0, 0, 0, .1);
    border-radius: .06rem;
    outline: none;
    font-size: .16rem;
    padding: 0 .16rem;
    box-sizing: border-box;

    &::placeholder {
      color: $content-notice-fontColor;
    }
  }

  &__login {
    line-height: .48rem;
    background: $btn-bgColor;
    box-shadow: 0 .04rem .08rem 0 rgba(0, 145, 255, 0.32);
    border-radius: .04rem;
    font-size: .16rem;
    color: $bgColor;
    text-align: center;
    margin: .16rem 0;
  }

  &__link {
    line-height: .2rem;
    text-align: center;
    font-size: .14rem;
    color: $content-notice-fontColor;

    &__gap {
      margin: 0 .12rem;
      font-size: .12rem;
      display: inline-block;
      transform: translateY(-.015rem)
    }

    a {
      text-decoration: none;
      color: $content-notice-fontColor;
    }
  }
}
</style>
